<template>
<div>
	<Checkbox title="" v-model="hobby">
		<CheckboxOption value=""></CheckboxOption>
		<CheckboxOption :value="1"></CheckboxOption>
		<CheckboxOption value=""></CheckboxOption>
		<CheckboxOption value=""></CheckboxOption>
		<CheckboxOption value=""></CheckboxOption>
	</Checkbox>
	<p>{{hobby}}</p>
</div>
</template>

<script>
export default {
	data() {
		return {
			hobby: [1]
		}
	},

	mounted() {
		setTimeout(() => {
			this.hobby = ['']
		}, 1000)
	},
}
</script>


