<template>
<div>
	<ul>
    <li>
      <a href="javascript:;" @click="open">Open ActionSheet</a>
    </li> 
  </ul>
</div>
</template>

<script>
export default {
	methods: {
		open() {
			const options =	[
        {
          text: '',
          onSelect: () => {
            console.log('')
          }
        },
        {
          text: '',
          destructive: true,
          onSelect: () => {
            console.log('')
          },
        },
        {
          text: '',
          disable: true
        }
	    ]
      this.$actionSheet.show('', options, false)
		}
	}
}
</script>


