<template>
<div>
	<ul>
		<li>
			username: {{username}}
		</li>
		<li>
			mobile: {{mobile}}
		</li>
		<li>
			birthday: {{birthday}}
		</li>
	</ul>
	<CellGroup>
		<Field placeholder="" align="left" v-model="username"/>
		<Field label="" type="number" placeholder="" align="left" v-model="mobile"/>
		<Field label="" type="date" placeholder="" align="left" v-model="birthday"/>
	</CellGroup>
</div>
</template>

<script>
export default {
	data() {
		return {
			username: '',
			mobile: '',
			birthday: ''
		}
	}
}
</script>


