<template>
<ul>
	<li>
		<a href="javascript:;" @click="alert">alert</a>
	</li>
	<li>
		<a href="javascript:;" @click="confirm">confirm</a>
	</li>
	<li>
		<a href="javascript:;" @click="prompt">prompt</a>
	</li>
</ul>
</template>

<script>
export default {
	methods: {
		alert() {
			this.$dialog.alert('', '', [
				{
					title: '',
					onClick() {}
				},
				{
					title: '',
					onClick() {}
				}
			])
		},
		confirm() {
			this.$dialog.confirm('', '', result => {
				alert(result)
			})
		},
		prompt() {
			this.$dialog.prompt('', result => {
				alert(result)
			})
		}
	}
}
</script>


