<template>
<div>
	<Radio title="" v-model="gender">
		<RadioOption value=""></RadioOption>
		<RadioOption value=""></RadioOption>
		<RadioOption value="" input></RadioOption>
	</Radio>
</div>
</template>

<script>
export default {
	data() {
		return {
			gender: null
		}
	},

	mounted() {
		setTimeout(() => {
			this.gender = ''
		}, 1000)
	}
}
</script>



