<template>
<div>
	<ul>
		<li>{{city}}</li>
	</ul>
	<Cell title="" arrow>
		<Selector slot="body" v-model="city" placeholder="">
			<option :value="item.val" v-for="item in options">{{item.text}}</option>
		</Selector>
	</Cell>
</div>	
</template>

<script>
export default {
	data() {
		return {
			city: "",
			options: []
		}
	},

	mounted() {
		setTimeout(() => {
			this.options = [
				{ text: '', val: 1 },
				{ text: '', val: 2 },
				{ text: '', val: 3 },
				{ text: '', val: 4 }
			]
		}, 100)
	}
}
</script>


