<template>
<div>
  <Cell :title="' ' + test1">
    <Switcher slot="body" onText="" offText="" v-model="test1" />
  </Cell>
  <Cell title="">
    <Switcher slot="body" v-model="test2"/>
  </Cell>
  <Cell :title="' ' + test3">
    <Switcher 
      slot="body" 
      onText="" 
      offText="" 
      onColor="rgb(19, 206, 102)" 
      offColor="rgb(255, 73, 73)"
      onVal=""
      offVal=""
      v-model="test3"
    />
  </Cell>
</div>
</template>

<script>
export default {
  data() {
    return {
      test1: true,
      test2: false,
      test3: ''
    }
  },

  mounted() {
    setTimeout(() => {
      this.test1 = false
    }, 1000)
  }
}
</script>


